<template>
  <div class="single">
    <div class="single-wrap mescroll">
      <!-- <h1 class="single-wrap-title">{{detail.title}}</h1> -->
      <div v-html="detail.tmsg" class="content">{{detail.tmsg}}</div>
    </div>
    <div class="tab">
      <tabbar />
    </div>
  </div>
</template>
<script>
import { getPage } from "@/api";
import Tabbar from "@/components/beautySalonMallTabber";
export default {
  components: {
    Tabbar,
  },
  data() {
    return {
      detail: {},
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });

      getPage({
        id: this.$route.query.id,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data.scCmsText;
          this.detail.tmsg = this.detail.tmsg.replace(
            /(<img[\s\S]+?)src=(['"])/g,
            '<img src="' + this.$config.http
          );
        } else {
          this.$toast(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = this.$route.query.title;
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.single {
  &-wrap {
    box-sizing: border-box;
    padding: 10px;
    height: calc(100vh - 50px);
    &-title {
      font-size: 16px;
      color: #000;
      margin: 0;
      font-weight: normal;
      margin: 10px 0;
      text-align: center;
      @include ellipsis-more;
    }
  }
  .tab {
    // height: 45px;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
  }
}
</style>

